.layout-boxed {
  background: url(../img/main/bg.png) #e1e2e8;
  font-size: 14px;
  color: #34495e;
  height: 100%; }

.pages {
  float: 12px;
  padding: 10px 20px;
  border-radius: 0px 0px 5px 5px;
  border: 1px solid #d7d8dd;
  border-top: 0px;
  color: #b0b0b0;
  overflow: hidden; }
  .pages .left {
    float: left; }
  .pages .right {
    float: right; }
    .pages .right a {
      float: right;
      width: 22px;
      height: 22px;
      display: inline-block;
      margin-left: 10px; }
  .pages .first {
    background: url(../img/main/bg_page.png) no-repeat 0 0; }
    .pages .first:hover {
      background-position: 0 -23px; }
  .pages .prev {
    background: url(../img/main/bg_page.png) no-repeat -22px 0px; }
    .pages .prev:hover {
      background-position: -22px -23px; }
  .pages .next {
    background: url(../img/main/bg_page.png) no-repeat -44px 0px; }
    .pages .next:hover {
      background-position: -44px -23px; }
  .pages .last {
    background: url(../img/main/bg_page.png) no-repeat -66px 0px; }
    .pages .last:hover {
      background-position: -66px -23px; }

.page_name {
  font-size: 24px;
  color: #b0b0b0;
  padding-top: 20px;
  padding-bottom: 10px; }

.button_box {
  padding: 10px 0px; }

.button_box_bottom {
  padding: 20px 0px;
  text-align: center; }

.blue_button {
  text-align: center;
  color: #fff;
  text-decoration: none;
  font-size: 14px;
  border-radius: 5px;
  padding: 0 30px;
  background: #00a78f;
  height: 30px;
  line-height: 30px;
  display: inline-block; }

.list_table_header {
  padding: 10px;
  border-radius: 5px 5px 0px 0px;
  border: 1px solid #d7d8dd;
  border-bottom: 2px solid #00a78f; }

div.select {
  display: -moz-inline-stack;
  display: inline-block;
  zoom: 1;
  *display: inline;
  vertical-align: bottom; }

div.select div, div.select div.open {
  background: url(../img/main/select-l.png) no-repeat 0 0;
  height: 28px;
  padding-left: 5px;
  font-size: 14px;
  color: #34495e;
  cursor: pointer;
  margin-right: -5px;
  font-family: 'Microsoft YaHei'; }

div.select div span, div.select div.open span {
  background: url(../img/main/select-r.png) no-repeat right 0;
  height: 28px;
  line-height: 28px;
  display: block; }

div.select div:hover {
  background: url(../img/main/select-l.png) no-repeat 0 0px; }

div.select div:hover span {
  background: url(../img/main/select-r.png) no-repeat right 0px; }

div.select div.open {
  background: url(../img/main/select-l.png) no-repeat 0 0px; }

div.select div.open span {
  background: url(../img/main/select-r.png) no-repeat right 0px; }

div.select ul {
  font-family: 'Microsoft YaHei';
  list-style: none;
  background-color: #FFFFFF;
  border-color: #d7d8dd #d7d8dd #d7d8dd;
  border-style: solid;
  border-width: 1px;
  margin: 0;
  padding: 0;
  position: absolute;
  z-index: 999;
  font-size: 14px;
  overflow: hidden; }

div.select li {
  padding: 0px 5px;
  cursor: default;
  height: 28px;
  line-height: 28px; }
  div.select li.hover {
    background: #d7d8dd;
    color: #6B6B6B; }

.unfold_arr {
  background: url(../img/main/icon_unfold.png) no-repeat;
  width: 22px;
  height: 13px;
  display: inline-block; }
  .unfold_arr:hover {
    background-position: left bottom; }

.list_table {
  border-left: 1px solid #d7d8dd;
  border-right: 1px solid #d7d8dd; }
  .list_table table tr {
    background: #efeff1;
    border-bottom: 1px solid #d7d8dd; }
    .list_table table tr td {
      padding: 6px 20px; }
    .list_table table tr:hover {
      background: #f9f9f9; }
  .list_table table th {
    padding: 6px 20px;
    color: #b0b0b0;
    text-align: left;
    font-weight: 100; }

.edit {
  background: url(../img/main/icon_edit.png) no-repeat;
  width: 18px;
  height: 18px;
  display: inline-block;
  margin: 0px 5px; }
  .edit:hover {
    background-position: left bottom; }

.delete {
  background: url(../img/main/icon_delete.png) no-repeat;
  width: 18px;
  height: 18px;
  display: inline-block;
  margin: 0px 5px; }
  .delete:hover {
    background-position: left bottom; }

.back {
  background: url(../img/main/icon_back.png) no-repeat;
  height: 24px;
  display: inline-block;
  padding-left: 29px;
  color: #00a78f;
  text-decoration: none; }
  .back:hover {
    background-position: left bottom;
    color: #ff8400; }

.form_table {
  border-radius: 5px;
  border: 1px solid #d7d8dd; }
  .form_table table tr td {
    padding: 6px 20px;
    vertical-align: top; }
  .form_table table .odd {
    background: #efeff1; }

.form-box {
  display: none; }

span.checkbox, span.checkbox-hover, span.checkbox-checked, span.checkbox-checked-hover {
  background: url(../img/main/bg_checkbox.png) 0 0 no-repeat;
  width: 18px;
  height: 18px;
