  margin: 0px;
  padding: 0px; }

span.checkbox-hover {
  background: url(../img/main/bg_checkbox.png) 0 0px no-repeat; }

span.checkbox-checked {
  background: url(../img/main/bg_checkbox.png) 0 -18px no-repeat; }

