  z-index: 0;
  margin: 0;
  padding: 0;
  width: 16px;
  height: 16px; }

input[type="text"] {
  font-family: 'Microsoft YaHei';
  height: 26px;
  line-height: 26px;
  border: 1px solid #d7d8dd;
  border-radius: 4px;
